  width: 35px;
  border-radius: 2px;
  background-color: #ff5722;
  position: relative;
  display: block;
  transition: all .2s ease-in-out;
}

body .grid-header .nav-bar .nav-wrap .nav-button span:before {
  top: -10px;
  visibility: visible;
}

body .grid-header .nav-bar .nav-wrap .nav-button span:after {
