}

body .grid-header .nav-bar .nav-wrap .nav-button span:before, body .grid-header .nav-bar .nav-wrap .nav-button span:after {
  height: 4px;
  width: 35px;
  border-radius: 2px;
  background-color: #ff5722;
  position: absolute;
  content: "";
  transition: all .2s ease-in-out;
}

body .grid-header .nav-bar .nav-wrap .nav-button:hover span,
body .grid-header .nav-bar .nav-wrap .nav-button:hover span:before,
body .grid-header .nav-bar .nav-wrap .nav-button:hover span:after {
