}

body .grid-header .nav-bar .nav-wrap .nav-button.open:hover span, body .grid-header .nav-bar .nav-wrap .nav-button.open span {
  background: transparent;
}

body .grid-header .nav-bar .nav-wrap .nav-button.open span:before {
