button {
  cursor: pointer;
  display: inline-block;
}

.click {
  padding: 30px 46px 27px 50px;
  border: 2px solid #fff;
  color: white;
  background-color: #ff5722;
  font-family: 'Lato', sans-serif;
  font-size: 28px;
  font-weight: 400;
  text-transform: uppercase;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.click:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.click:hover, .click:active, .click:focus {
  color: #ff5722;
}

.click:hover:before, .click:active:before, .click:focus:before {
  -webkit-transform: scale(1);
  transform: scale(1);
}

.learn {
  padding: 19px 30px 25px;
  background: #ff5722;
  color: white;
  border: 2px solid #ff5722;
  font-family: 'OpenSans', sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.learn::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.learn:hover, .learn:active, .learn:focus {
  color: #ff5722;
}

.learn:hover:before, .learn:active:before, .learn:focus:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.owl-nav {
  position: absolute;
  bottom: 6%;
  left: 19%;
}

.owl-nav .owl-prev,
.owl-nav .owl-next {
  display: inline-block;
  margin: 0 1px;
  color: rgba(0, 0, 0, 0.39);
  font-size: 32px;
  padding: 18px 27px 14px 20px;
  background: rgba(255, 255, 255, 0.36);
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.owl-nav .owl-prev:hover,
.owl-nav .owl-next:hover {
  background: #ff5722;
  color: #fff;
}
